'use client';
import Link from "next/link";
import Image from "next/image";
import "@/app/css/Navbar.css";
import { useEffect, useState } from "react";
import Login from "@/app/pages/Login";

export default function Navbar() {
    // Caso Display no sea el mejor
    const [isOpen, setIsOpen] = useState(false);
    const [isLogged, setIsLogged] = useState(false);
    //Lectura de cookies(JWT valido)
    useEffect(() => {
        fetch("/api/usuario/logged", {
            method: "GET",
            credentials: "include", // Importante para enviar cookies httpOnly
        })
        .then(res => res.json())
        .then(data => {
            setIsLogged(data.logged);
        });
    }, []);
    return (
        <nav>
            {/* LOGO */}
            <div className="NavbarLogo">
                <Link href="/">
                    <Image src="/ScorpionWebPoke.png" width={60} height={60} alt="PokeLogo" />
                </Link>
                <h1>PokeScorpion</h1>
            </div>

            {/* BOTÓN HAMBURGUESA */}
            <div className="menu-toggle" onClick={() => setIsOpen(!isOpen)}>
                <span></span>
                <span></span>
                <span></span>
            </div>

            {/* RUTAS */}
            <div className={`Rutas ${isOpen ? "active" : ""}`}>
                <Link href="/">Qué es PokeScorpion?</Link>
                <Link href = {Login}>Inicia Sesión</Link>
                <Link href="/">¡Regístrate!</Link>
            </div>
        </nav>
    );
}
